export default {
	data() {
		return {
			username: '',
			password: '',
			department: '',
			pwdType: 'password',
			btnDisabled: false,
			switchImg: '/static/image/icon_pwd_hide.png',
			btnColor: '#91C5F7',
			hostName: '',
			regUserId: '544784',
			platform: '',
			userInfo: {}
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				this.platform = res.platform;
			}
		});	
		if (this.username !=='' && this.username !== null) {
			this.getDevlop();
		}
	},
	watch: {
		username(newValue, oldValue) {
			if (newValue !=='' && newValue !== null) {
				this.getDevlop();
			}
		}
	},
	methods: {
		checkInput () {
			if (this.username !== '' && this.password !== '' && this.department !== '' && this.username !== null && this.password !== null && this.department !== null) {
				this.btnDisabled = true;
				this.btnColor = '#427CAC';
			} else {
				this.btnDisabled = false;
					this.btnColor = '#91C5F7';
			}
		},
		checkUser(e) {
			this.username = e.target.value;
			this.department = '';
			this.checkInput();
		},
		checkPwd(e) {
			this.password = e.target.value;
			this.checkInput();
		},
		delUser(e) {
			this.username = '';
			this.password = '';
			this.department = '';
			this.btnColor = '#91C5F7';
		},
		switchPwd(e) {
			if (this.pwdType == 'text') {
				this.pwdType = 'password';
				this.switchImg = '/static/image/icon_pwd_hide.png'
			} else {
				this.pwdType = 'text';
				this.switchImg = '/static/image/icon_pwd_show.png'
			}
		},
		getDevlop () {
			let para = {no: this.username};	1
			this.$API.get('/fuxi/login/query-user-department', para).then(res => {
				if (res.code === 'success') {
					this.department = res.data;
					this.checkInput();
				}
			});
		},
		login() { // 登录成功
			if (this.btnDisabled) {
				console.log(23232);
				let userInfo = {no: this.username, password: this.password, hostName: this.platform,  regUserId: this.regUserId};
				this.$API.get('/fuxi/login/login', userInfo, 'POST').then(res => {
					if (res.code === 'success') {
						this.userInfo = res.data;
						this.$API.setStorage('fuxiUserInfo', res.data);
						 uni.showToast({
							title: '登录成功,即将跳转...',
							icon: 'none',
							duration: 2500,
							success: () => {
								let timer = setTimeout(() => {
									clearInterval(timer);
									this.$API.tab('../tab/main/main');
								}, 2000);
							}
						});
					} else {
					  uni.showToast({
					  	title: '' + res.msg,
						icon: 'none'
					  });
					}
				});
			}
		}
	}
}
